const Header = props => {
    return (
        <div
            className={`item sticky ${props.id === 0 ? '' : 'bg--dark'} item-${
                props.id
            } ${props.getClassNamesFor(props.id)} `}
            onClick={props.onClick.bind(this, props.id)}
        >
            {props.name}
        </div>
    );
};

export default Header;
