import Table from '../features/maintable/views/Table';
import Main from './Main';
import { Navigate } from 'react-router-dom';
import Edit from '../features/editform/views/Edit';

export const publicRoutes = [
    {
        path: '/',
        element: (<Navigate
            to={window.baseUrl + '/app/dashboard' + '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport'}/>)
    },
    {
        path: window.baseUrl,
        element: (<Navigate
            to={window.baseUrl + '/app/dashboard' + '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport'}/>)
    },
    {
        path: window.baseUrl + '/app',
        element: <Main />,
        children: [
            {
                children: [
                    {
                        path: 'dashboard',
                        element: <Table />
                    },
                    {
                        path: 'add',
                        element: <Edit />
                    },
                    {
                        path: 'edit/:stateId/:mid',
                        element: <Edit />
                    }
                ],
            },
        ],
    },
];
