import React, { useCallback, useState, useEffect } from 'react';
import MainContent from '../../../components/MainContent';
import AccordionItem from '../../../components/AccordionItem';
import useStore from '../../../stores/useStore';
import shallow from 'zustand/shallow';

const getState = state => [state.stateData, state.userList];

const Accordion = props => {
    const [showAccordion, setShowAccordion] = useState(false);
    const [filteredData, setFilteredData] = useState([]);
    const [stateData, userList] = useStore(getState, shallow);
    const [countAwait, setCountAwait] = useState(null);
    const [countApproved, setCountApproved] = useState(null);

    const handleAccardion = useCallback(() => {
        setShowAccordion(previousValue => !previousValue);
        let data = stateData[props.id];
        if (data) {
            data = data.map(x => {
                const user = userList.find(y => y.NID == x.WHOAPPLIED);
                if (user) x.WHOAPPLIEDNAME = user.NAME;
                return x;
            });
            data.pop();
            setFilteredData(data);
        }
    }, [showAccordion, stateData]);

    const setCounts = useCallback(
        data => {
            if (data) {
                setCountAwait(data[data.length - 1].countAwait);
                setCountApproved(data[data.length - 1].countApproved);
            }
        },
        [countAwait, countApproved]
    );

    useEffect(() => {
        setCounts(stateData[props.id]);
    }, []);

    const handleSortStatus = name => {
        let updatedData = [];

        stateData[props.id].map((node, i, data) => {
            if (i + 1 !== data.length) {
                if (node.STATUS.toLowerCase() === name) {
                    updatedData.push(node);
                }
            }
        });

        setFilteredData(updatedData);
    };

    return (
        <React.Fragment>
            <AccordionItem
                handleSortStatus={handleSortStatus}
                countAwait={countAwait}
                countApproved={countApproved}
                onClick={handleAccardion}
                accordion={showAccordion}
                addNewItem={() => props.addNew(props.id)}
                element={props.id + '. ' + props.name}
            />
            {filteredData &&
                filteredData.map(el => {
                    return showAccordion ? (
                        <MainContent
                            stateId={props.id}
                            key={el.MID}
                            data={el}
                            columnNum={props.tableColumns}
                            showAttachments={() =>
                                props.showAttachments(props.id, el.MID)
                            }
                        ></MainContent>
                    ) : null;
                })}
        </React.Fragment>
    );
};

export default Accordion;
