import useStore  from '../../../stores/useStore';
import shallow from 'zustand/shallow';
import EditComponent from './EditComponent';
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import { useParams } from "react-router-dom";
import { useCallback, useEffect, useState } from 'react';

const getState = state => [
    state.states,
    state.stateData,
    state.subject,
    state.selectedState,
    state.changeSubject,
    state.changeState,
    state.whoApplied,
    state.changeWhoApplied,
    state.applyDate,
    state.changeApplyDate,
    state.toSecretary,
    state.changeToSecretary,
    state.additionalCommn,
    state.changeSetAdditionalCommn,
    state.decisionStatus,
    state.changeDecisionStatus,
    state.approveDate,
    state.changeApproveDate,
    state.protocolDate,
    state.changeProtocolDate,
    state.protocol,
    state.changeProtocol,
    state.note1,
    state.changeNote1,
    state.tasks,
    state.changeTasks,
    state.steps,
    state.changeSteps,
    state.executionStatus,
    state.changeExecutionStatus,
    state.note2,
    state.changeNote2,
    state.note3,
    state.changeNote3,
    state.changeFiles,
    state.insertAppeal,
    state.assignCatToAttch,
    state.updateAppeal,
    state.getStateData,
    state.desicionStatusTypes,
    state.getAttachments,
    state.deleteAttachment,
    state.createdFileIds,
    state.changeFileIds,
    state.userList
]

const Edit = () => {
    const [
        states,
        stateData, 
        subject, 
        selectedState,
        changeSubject,
        changeState,
        whoApplied,
        changeWhoApplied,
        applyDate,
        changeApplyDate,
        toSecretary,
        changeToSecretary,
        additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus,
        changeDecisionStatus,
        approveDate,
        changeApproveDate,
        protocolDate,
        changeProtocolDate,
        protocol,
        changeProtocol,
        note1,
        changeNote1,
        tasks,
        changeTasks,
        steps,
        changeSteps,
        executionStatus,
        changeExecutionStatus,
        note2,
        changeNote2,
        note3,
        changeNote3,
        changeFiles,
        insertAppeal,
        assignCatToAttch,
        updateAppeal,
        getStateData,
        desicionStatusTypes,
        getAttachments,
        deleteAttachment,
        createdFileIds,
        changeFileIds,
        userList
    ] = useStore(getState, shallow);
    const [attachments, setAttachments] = useState([]);
    const [editData, setEditData] = useState();
    const navigate = useNavigate();
    const params = useParams();
    
    const addNewAppeal = async () => {
        showLoader();
        let appeal = {
            mid: uuidv4(),
            nzid: selectedState,
            subject: subject,
            whoapplied: whoApplied,
            applydate: applyDate,
            tosecretary: toSecretary,
            additionalcommn: additionalCommn,
            status: decisionStatus,
            protocol: protocol,
            approvedate: approveDate,
            protocoldate: protocolDate,
            note1: note1,
            tasksbyboard: tasks,
            stepsbykim: steps,
            taskstatus: executionStatus,
            note2: note2,
            notebyaih: note3,
            enterdate: moment(new Date()).format('MM/DD/YYYY'),
            performerid: window.currentUserId
        }
        if(params.stateId && params.mid){
            appeal.mid = params.mid;
            appeal.seq = editData.SEQ;
            await updateAppeal(appeal);
        }else{
            await insertAppeal(appeal);
        }
        await assignCatToAttch({mid: appeal.mid, nzid: appeal.nzid, createdFileIds: createdFileIds});
        await getStateData();
        setTimeout(()=> {
            closeLoader(); 
            navigate(window.baseUrl + '/app/dashboard' + '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport')
        }, 1000);
    }
    let dataObj = {
        selectedState: selectedState,
        subject: subject,
        whoApplied: whoApplied,
        applyDate: moment(applyDate).format('YYYY-MM-DD'),
        toSecretary: moment(toSecretary).format('YYYY-MM-DD'),
        additionalCommn: additionalCommn,
        decisionStatus: decisionStatus,
        approveDate: moment(approveDate).format('YYYY-MM-DD'),
        protocolDate: moment(protocolDate).format('YYYY-MM-DD'),
        protocol: protocol,
        note1: note1,
        tasks: tasks,
        steps: steps,
        executionStatus: executionStatus,
        note2: note2,
        note3: note3
    }
    const getAttch = useCallback(async () => {
        const result = await getAttachments(params.mid);
        const attachments = result.map(x=>{
            const name = x.AttchName;
            const index = name.lastIndexOf("_");
            const cutName = name.substring(0, index);
            x.AttchName = cutName;
            return x;
        })
        setAttachments(attachments);
        const files = attachments.map(x=>x.AttchId);
        changeFileIds(files);
    }, [attachments, createdFileIds]);
    const updateEditData = useCallback(()=>{
        let data = null;
        try{
            if(!editData && params.stateId && params.mid){
                data = stateData[params.stateId]?.find(x=>x.MID === params.mid);
                setEditData(data);
            }
        }catch(e){
            console.error(e);
        }
        if(data){ 
            changeState(params.stateId);
            if(data.SUBJECT !== '?'){
                changeSubject(data.SUBJECT);
            }
            if(data.WHOAPPLIED !== '?'){
                changeWhoApplied(data.WHOAPPLIED);
            }
            if(data.APPLYDATE !== '?'){
                const applyDate = moment(data.APPLYDATE).format('YYYY-MM-DD');
                changeApplyDate(applyDate);
            }
            if(data.TOSECRETARY !== '?'){
               const toSecretary =  moment(data.TOSECRETARY).format('YYYY-MM-DD');
               changeToSecretary(toSecretary);
            }
            if(data.ADDITIONALCOMMN !== '?'){
                const additionalCommn = data.ADDITIONALCOMMN;
                changeSetAdditionalCommn(additionalCommn);
            }
            if(data.STATUS !== '?'){
                changeDecisionStatus(data.STATUS);
            }
            if(data.APPROVEDATE !== '?'){
                const approveDate = moment(data.APPROVEDATE).format('YYYY-MM-DD');
                changeApproveDate(approveDate);
            }
            if(data.PROTOCOLDATE !== '?'){
                const protocolDate = moment(data.PROTOCOLDATE).format('YYYY-MM-DD');
                changeProtocolDate(protocolDate);
            }
            if(data.PROTOCOL !== '?'){
                changeProtocol(data.PROTOCOL);
            }
            if(data.NOTE1 !== '?'){
                changeNote1(data.NOTE1);
            }
            if(data.TASKSBYBOARD !== '?'){
                changeTasks(data.TASKSBYBOARD);
            }
            if(data.STEPSBYKIM !== '?'){
                changeSteps(data.STEPSBYKIM);
            }
            if(data.TASKSTATUS !== '?'){
                changeExecutionStatus(data.TASKSTATUS);
            }
            if(data.NOTE2 !== '?'){
                changeNote2(data.NOTE2);
            }
            if(data.NOTEBYAIH !== '?'){
                changeNote3(data.NOTEBYAIH);
            }
        }
    }, [
        subject, 
        selectedState,
        whoApplied, 
        applyDate,
        toSecretary,
        additionalCommn,
        decisionStatus,
        approveDate,
        protocolDate,
        protocol,
        note1,
        tasks,
        steps,
        executionStatus,
        note2,
        note3, 
        editData
    ])
    useEffect(()=>{ 
        updateEditData();
        getAttch();
    }, [])
    
    const showLoader = () => {
        window.JQUERY("#loadingModalForSave").modal('show')
    }
    const closeLoader = () => {
        window.JQUERY("#loadingModalForSave").modal('hide')
    }
    const showAttachments = () => {
        window.JQUERY("#attachmentModal").modal('show');
    }
    const hideAttachments = () => {
        window.JQUERY("#attachmentModal").modal('hide');
    }

    const handleRemoveAttachment = async attchId => {
        await deleteAttachment(attchId);
        const removeAttchList = attachments.filter(x=>x.AttchId !== attchId);
        setAttachments(removeAttchList);
    }
    const args = {
        states,
        subject: dataObj.subject,
        selectedState: dataObj.selectedState,
        changeSubject,
        changeState,
        whoApplied: dataObj.whoApplied,
        changeWhoApplied,
        applyDate: dataObj.applyDate,
        changeApplyDate,
        toSecretary: dataObj.toSecretary,
        changeToSecretary,
        additionalCommn: dataObj.additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus: dataObj.decisionStatus,
        changeDecisionStatus,
        approveDate: dataObj.approveDate,
        changeApproveDate,
        protocolDate: dataObj.protocolDate,
        changeProtocolDate,
        protocol: dataObj.protocol,
        changeProtocol,
        note1: dataObj.note1,
        changeNote1,
        tasks: dataObj.tasks,
        changeTasks,
        steps: dataObj.steps,
        changeSteps,
        executionStatus: dataObj.executionStatus,
        changeExecutionStatus,
        note2: dataObj.note2,
        changeNote2,
        note3: dataObj.note3,
        changeNote3,
        changeFiles,
        addNewAppeal,
        desicionStatusTypes,
        attachments,
        showAttachments,
        hideAttachments,
        removeAttachment: handleRemoveAttachment,
        userList
    }
    return (
       <EditComponent {...args} />
    );
};

export default Edit;
