import Header from './Header';

const HeaderLine = props => {
    return props.columnHeaders.map(el => {
        return (
            <Header
                key={el.id}
                id={el.id}
                name={el.name}
                onClick={props.onClick}
                getClassNamesFor={props.getClassNamesFor}
            />
        );
    });
};

export default HeaderLine;
