import React, { useEffect, useState } from "react";
import {Outlet} from "react-router-dom";
import useStore  from '../stores/useStore.js';
import shallow from 'zustand/shallow';

const getState = state => [
    state.getStates,
    state.getStateData,
    state.auth,
    state.getUserList
]

const Main = () => {
    const [ getStates, getStateData, auth, getUserList ] = useStore(getState, shallow);
    const [done, setDone] = useState(false);
    useEffect(()=>{
        const getFromDB = async () => {
            await auth();
            await getUserList();
            await getStates();
            await getStateData();
            setDone(true);
        }
        getFromDB();
    })

    return (
        <>
            {
                done ? <Outlet/> : <p>Loading...</p>
            }
        </>
    );
}

export default Main;