import { useNavigate } from 'react-router-dom';

const ButtonElement = props => {
    const navigate = useNavigate();

    const goToEditPanel = e => {
        let url = window.baseUrl + '/app/edit';
        if(props.stateId && props.mid){
            url += `/${props.stateId}/${props.mid}`;
        }
        url += '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport';
        navigate(url, { state: { editType: props.editType } });
    };
    return (
        <button className={props.className} onClick={e => goToEditPanel()}>
            {props.children}
        </button>
    );
};

export default ButtonElement;
