import { useNavigate } from 'react-router-dom';
import Loading from '../components/Loading';
import Attachments from '../../maintable/components/Attachments';
import { useEffect, useState } from 'react';
import autosize from 'autosize';

const EditComponent = props => {
    const navigate = useNavigate();
    const [textarea, setTextarea] = useState();
    const [textarea1, setTextarea1] = useState();
    const [textarea2, setTextarea2] = useState();
    const [textarea3, setTextarea3] = useState();
    const [textarea4, setTextarea4] = useState();
    const [textarea5, setTextarea5] = useState();
    const [textarea6, setTextarea6] = useState();

    useEffect(() => {
        autosize(textarea);
    }, [textarea]);

    useEffect(() => {
        autosize(textarea1);
    }, [textarea1]);

    useEffect(() => {
        autosize(textarea2);
    }, [textarea2]);

    useEffect(() => {
        autosize(textarea3);
    }, [textarea3]);

    useEffect(() => {
        autosize(textarea4);
    }, [textarea4]);

    useEffect(() => {
        autosize(textarea5);
    }, [textarea5]);

    useEffect(() => {
        autosize(textarea6);
    }, [textarea6]);

    return (
        <div id="body">
            <main className="content">
                <h1 className="edit--header">Redaktə forması</h1>
                <button
                    className="edit--return-button"
                    onClick={() => navigate(-1)}
                >
                    <i className="fas fa-long-arrow-left"></i>Geri dön
                </button>
                <form>
                    <div className="form-group">
                        <div className="form-row row">
                            <div className="form-group col-md-6">
                                <label>Başlıq</label>
                                <select
                                    className="form-control"
                                    value={props.selectedState}
                                    onChange={e =>
                                        props.changeState(e.target.value)
                                    }
                                >
                                    <option value="none" disabled>
                                        Choose...
                                    </option>
                                    {props.states.length > 0 &&
                                        props.states.map(x => {
                                            return (
                                                <option key={x.id} value={x.id}>
                                                    {' '}
                                                    {x.name}{' '}
                                                </option>
                                            );
                                        })}
                                </select>
                            </div>
                            <div className="form-group col-4">
                                <label>Müraciət edən</label>
                                <select
                                    className="form-control"
                                    value={props.whoApplied}
                                    onChange={e =>
                                        props.changeWhoApplied(e.target.value)
                                    }
                                >
                                    <option value="none">
                                        Choose...
                                    </option>
                                    {props.userList.length > 0 &&
                                        props.userList.map(x => {
                                            return (
                                                <option key={x.NID} value={x.NID}>
                                                    {' '}
                                                    {x.NAME}{' '}
                                                </option>
                                            );
                                        })}
                                </select>
                            </div>
                        </div>
                    </div>
                    <div className="form-row row">
                        <div className="form-group col-md-6">
                            <label>Mövzu</label>
                            <input
                                type="text"
                                className="form-control"
                                value={props.subject}
                                onChange={e =>
                                    props.changeSubject(e.target.value)
                                }
                            />
                        </div>
                        <div className="form-group col-4">
                            <label>Müraciət tarixi</label>
                            <input
                                type="date"
                                className="form-control"
                                value={props.applyDate}
                                onChange={e =>
                                    props.changeApplyDate(e.target.value)
                                }
                            />
                        </div>
                    </div>
                    <div className="form-row row">
                        <div className="form-group col-6">
                            <label>Əlavə kommunikasiya tədbirləri</label>
                            <textarea
                                ref={a => setTextarea(a)}
                                className="form-control"
                                rows={1}
                                value={props.additionalCommn}
                                onChange={e =>
                                    props.changeSetAdditionalCommn(
                                        e.target.value
                                    )
                                }
                            ></textarea>
                        </div>
                        <div className="form-group col-4">
                            <label>Təqdim edilmiş sənədlər</label>
                            <input
                                type="file"
                                className="form-control"
                                multiple
                                onChange={e =>
                                    props.changeFiles(e.target.files)
                                }
                            />
                            <br /> 
                            {
                                props.attachments.length > 0 ? 
                                <>
                                    <button style={{color: "black"}} className="form-control btn btn-secondary" onClick={e=>{e.preventDefault(); props.showAttachments();}}>Əlavələr</button>
                                </> : <></>                             
                            }
                        </div>
                    </div>

                    <div className="form-row row">
                        <div className="form-group col-2">
                            <label>Protokol №</label>
                            <input
                                type="number"
                                className="form-control"
                                value={props.protocol}
                                onChange={e =>
                                    props.changeProtocol(e.target.value)
                                }
                            />
                        </div>
                        <div className="form-group col-4">
                            <label>Protokol təsdiq tarixi</label>
                            <input
                                type="date"
                                className="form-control"
                                value={props.approveDate}
                                onChange={e =>
                                    props.changeApproveDate(e.target.value)
                                }
                            />
                        </div>
                        <div className="form-group col-4">
                            <label>Protokol daxil olub</label>
                            <input
                                type="date"
                                className="form-control"
                                value={props.protocolDate}
                                onChange={e =>
                                    props.changeProtocolDate(e.target.value)
                                }
                            />
                        </div>
                    </div>

                    <div className="form-row row">
                        <div className="form-group col-6">
                            <label>Qeyd</label>
                            <textarea
                                ref={c => setTextarea1(c)}
                                rows={1}
                                className="form-control"
                                value={props.note1}
                                onChange={e =>
                                    props.changeNote1(e.target.value)
                                }
                            ></textarea>
                        </div>
                        <div className="form-group col-4">
                            <label>
                                Müraciətin Korporativ Katibə təqdim edilməsi
                            </label>
                            <input
                                type="date"
                                className="form-control"
                                value={props.toSecretary}
                                onChange={e =>
                                    props.changeToSecretary(e.target.value)
                                }
                            />
                        </div>
                    </div>

                    <div className="form-row row">
                        <div className="form-group col-6">
                            <label>
                                MŞ-nın qərarından irəli gələn tapşırıqlar
                            </label>
                            <textarea
                                ref={c => setTextarea2(c)}
                                rows={1}
                                className="form-control"
                                value={props.tasks}
                                onChange={e =>
                                    props.changeTasks(e.target.value)
                                }
                            ></textarea>
                        </div>
                        <div className="form-group col-4">
                            <label>MŞ-nın qərarının statusu</label>
                            <select
                                className="form-control"
                                value={props.decisionStatus}
                                onChange={e =>
                                    props.changeDecisionStatus(e.target.value)
                                }
                            >
                                <option value="none" disabled>
                                    Choose...
                                </option>
                                {props.desicionStatusTypes.length > 0 &&
                                    props.desicionStatusTypes.map(x => {
                                        return (
                                            <option key={x.id} value={x.name}>
                                                {' '}
                                                {x.name}{' '}
                                            </option>
                                        );
                                    })}
                            </select>
                        </div>
                    </div>

                    <div className="form-row row">
                        <div className="form-group col-6">
                            <label>
                                Tapşırıqların icrası istiqamətində KİM
                                tərəfindən atılan addımlar
                            </label>
                            <textarea
                                ref={c => setTextarea3(c)}
                                rows={1}
                                className="form-control"
                                value={props.steps}
                                onChange={e =>
                                    props.changeSteps(e.target.value)
                                }
                            ></textarea>
                        </div>
                        <div className="form-group col-4">
                            <label>Qeyd 2</label>
                            <textarea
                                ref={c => setTextarea4(c)}
                                rows={1}
                                className="form-control"
                                value={props.note2}
                                onChange={e =>
                                    props.changeNote2(e.target.value)
                                }
                            ></textarea>
                        </div>
                    </div>

                    <div className="form-row row">
                        <div className="form-group col-6">
                            <label>
                                MŞ-nın qərarından irəli gələn tapşırıqların icra
                                statusu
                            </label>
                            <textarea
                                ref={c => setTextarea5(c)}
                                rows={1}
                                className="form-control"
                                value={props.executionStatus}
                                onChange={e =>
                                    props.changeExecutionStatus(e.target.value)
                                }
                            ></textarea>
                        </div>

                        <div className="form-group col-4">
                            <label>Qeyd 3</label>
                            <textarea
                                ref={c => setTextarea6(c)}
                                rows={1}
                                className="form-control"
                                value={props.note3}
                                onChange={e =>
                                    props.changeNote3(e.target.value)
                                }
                            ></textarea>
                        </div>
                    </div>
                    <div className="form-group row">
                        <div className="col-sm-10 button-right">
                            <button
                                type="button"
                                className="btn-save"
                                onClick={props.addNewAppeal}
                            >
                                Əlavə et
                            </button>
                            {/* <button type="button" className="btn-reset">
                                Reset
                            </button> */}
                        </div>
                    </div>
                </form>
            </main>
            <Loading />
            <Attachments hide={props.hideAttachments} attachments={props.attachments} hasDelete={true} changeFiles={props.changeFiles} deleteAttachment={props.removeAttachment} />
        </div>
    );
};

export default EditComponent;
