import { useState } from "react";

const Attachments = props => {
  const [attchId, setAttchId] = useState();
  const areYouSure = id => {
    setAttchId(id);
    showAreYouSure();
  }
  const  showAreYouSure = () => {
    window.JQUERY("#attachmentModal").modal('hide');
    window.JQUERY("#areYouSureModal").modal('show');
  }
  const hideAreYouSure = () => {
    window.JQUERY("#areYouSureModal").modal('hide');
  }
  const returnToAttchModal = () => {
    window.JQUERY("#areYouSureModal").modal('hide');
    window.JQUERY("#attachmentModal").modal('show');
  }
    return (
      <>
        <div className="modal fade" id="attachmentModal" role="dialog" aria-labelledby="attachmentModalCenterTitle" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalCenterTitle">Əlavələr</h5>
              <button type="button" className="close" aria-label="Close" onClick={props.hide}>
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
              <div className="modal-body">
                  {
                      props.attachments.length > 0 ? props.attachments.map(x=>{
                        return <div className="d-flex">
                          <div dangerouslySetInnerHTML={{__html: x.AttchGif}} />
                          <a href={x.AttchDownloadUrl} target="_blank">{x.AttchName}</a>
                          <div dangerouslySetInnerHTML={{__html: x.AttchFuncMenu}} />
                          {
                            props.hasDelete ? <button className="btn" onClick={()=>areYouSure(x.AttchId)}><i className="fa fa-trash" aria-hidden="true"></i></button>
                                            : <></>
                          }           
                          <br/>
                        </div>
                      }) : <><p>🚫Əlavə mövcud deyil</p></>
                  }
              </div>
            </div>
          </div>
        </div>
        <div className="modal fade" id="areYouSureModal" role="dialog" aria-labelledby="areYouSureModalareYouSureModalCenterTitle" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-body d-flex justify-content-center">
                  <h1>Əminsiniz</h1>
              </div>
              <div className="modal-footer d-flex justify-content-center">
                <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={()=>returnToAttchModal()}>Xeyr</button>
                <button type="button" className="btn btn-success" onClick={()=>{hideAreYouSure(); props.deleteAttachment(attchId);}}>Bəli</button>
              </div>
            </div>
          </div>
        </div>
      </>
    )
}

export default Attachments;