import React from 'react';

import ButtonElement from './ButtonElement';
import MainElement from './DataCell';
import moment from 'moment';

const MainContent = props => {
    const applyDate = props.data.APPLYDATE !== '?' ? moment(props.data.APPLYDATE).format('DD/MM/YYYY') : '';
    const toSecretary = props.data.TOSECRETARY !== '?' ? moment(props.data.TOSECRETARY).format('DD/MM/YYYY') : '';
    const enterDate = props.data.ENTEREDATE !== '?' ? moment(props.data.ENTEREDATE).format('DD/MM/YYYY') : '';
    const protocolDate = props.data.PROTOCOLDATE !== '?' ? moment(props.data.PROTOCOLDATE).format('DD/MM/YYYY') : '';
    return (
        <React.Fragment>
            <MainElement
                id="0"
                element={props.data.SUBJECT !== '?' ? props.data.SUBJECT : ''}
                style={props.style}
            >
                <ButtonElement className="item-edit" editType="save" stateId={props.stateId} mid={props.data.MID}>
                    <i className="fas fa-pen"></i>
                </ButtonElement>
            </MainElement>
            <MainElement
                id="1"
                element={props.data.WHOAPPLIEDNAME !== undefined ? props.data.WHOAPPLIEDNAME : ''}
                style={props.style}
            />
            <MainElement
                id="2"
                element={ applyDate }
                style={props.style}
            />
            <MainElement
                id="3"
                element={ toSecretary }
                style={props.style}
            />
            <button onClick={()=> props.showAttachments()}><i className="fa fa-paperclip"></i></button>
            <MainElement
                id="5"
                element={props.data.ADDITIONALCOMMN !== '?' ? props.data.ADDITIONALCOMMN : ''}
                style={props.style}
            />
            <MainElement
                id="6"
                element={props.data.STATUS !== '?' ? props.data.STATUS : ''}
                style={{
                    backgroundColor:
                        props.data.STATUS.toLowerCase() === 'gözləmədədir'
                            ? '#E8E9A1'
                            : '#C6EBC9',
                }}
            />
            <MainElement
                id="7"
                element={props.data.PROTOCOL !== '?' ? props.data.PROTOCOL : ''}
                style={props.style}
            />
            <MainElement
                id="8"
                element={ enterDate }
                style={props.style}
            />
            <MainElement
                id="9"
                element={ protocolDate }
                style={props.style}
            />
            <MainElement id="10" element={props.data.NOTE1 !== '?' ? props.data.NOTE1 : ''} style={props.style} />
            <MainElement id="11" element={props.data.TASKSBYBOARD !== '?' ? props.data.TASKSBYBOARD : ''} style={props.style} />
            <MainElement id="12" element={props.data.STEPSBYKIM !== '?' ? props.data.STEPSBYKIM : ''} style={props.style} />
            <MainElement id="13" element={props.data.TASKSTATUS !== '?' ? props.data.TASKSTATUS : ''} style={props.style} />
            <MainElement id="14" element={props.data.NOTE2 !== '?' ? props.data.NOTE2 : ''} style={props.style} />
            <MainElement id="15" element={props.data.NOTEBYAIH !== '?' ? props.data.NOTEBYAIH : ''} style={props.style} />
        </React.Fragment>
    );
};

export default MainContent;
