import data from '../../../MOCK_DATA.json';
import React, { useState, useEffect } from 'react';
import TableComponent from './TableComponent';
import { useNavigate } from 'react-router-dom';
import useStore from '../../../stores/useStore';
import shallow from 'zustand/shallow';
import useSortableData from '../../../hooks/use-sortableData';
import xlsx from 'json-as-xlsx';

const getState = state => [state.states, state.stateData, state.stateDataAll, state.changeState, state.clearStore, state.getAttachments];

const Table = () => {
    const [filteredData, setFilteredData] = useState([]);
    const { items, requestSort, sortConfig } = useSortableData(filteredData);
    const [states, stateData, stateDataAll, changeState, clearStore, getAttachments] = useStore(getState, shallow);
    const [attachments, setAttachments] = useState([]);
    const navigate = useNavigate();

    const tableColumns = [
        { id: 0, name: 'Mövzu' },
        { id: 1, name: 'Müraciət edən' },
        { id: 2, name: 'Müraciət tarixi' },
        { id: 3, name: 'Müraciətin Korporativ Katibə təqdim edilməsi' },
        { id: 4, name: 'Təqdim edilmiş sənədlər' },
        { id: 5, name: 'Əlavə kommunikasiya tədbirləri' },
        { id: 6, name: 'MŞ-nın qərarının statusu' },
        { id: 7, name: 'Protokol №' },
        { id: 8, name: 'Təsdiq tarixi' },
        { id: 9, name: 'Protokol daxil olub' },
        { id: 10, name: 'Qeyd' },
        { id: 11, name: 'MŞ-nın qərarından irəli gələn tapşırıqlar' },
        {
            id: 12,
            name: 'Tapşırıqların icrası istiqamətində KİM tərəfindən atılan addımlar',
        },
        {
            id: 13,
            name: 'MŞ-nın qərarından irəli gələn tapşırıqların icra statusu',
        },
        { id: 14, name: 'Qeyd 2' },
        { id: 15, name: 'Qeyd 3' },
    ];

    const addNew = id => {
        changeState(id);
        clearStore();
        navigate(
            window.baseUrl +
                '/app/add' +
                '?func=ll&objId=' +
                window.currentWebreportId +
                '&objAction=RunReport'
        );
    };

    const clickAndDrag = e => {
        if (e.target.classList.contains('accordion')) {
            const slider = document.querySelector('.main-container');
            let isDown = false;
            let startX;
            let scrollLeft;

            slider.addEventListener('mousedown', e => {
                isDown = true;
                slider.classList.add('active');
                startX = e.pageX - slider.offsetLeft;
                scrollLeft = slider.scrollLeft;
            });
            slider.addEventListener('mouseleave', () => {
                isDown = false;
                slider.classList.remove('active');
            });
            slider.addEventListener('mouseup', () => {
                isDown = false;
                slider.classList.remove('active');
            });
            slider.addEventListener('mousemove', e => {
                if (!isDown) return;
                e.preventDefault();
                const x = e.pageX - slider.offsetLeft;
                const walk = (x - startX) * 3; //scroll-fast
                slider.scrollLeft = scrollLeft - walk;
            });
        } else {
            //console.log('hey');
        }
    };

    const getClassNamesFor = id => {
        if (!sortConfig) {
            return;
        }

        return sortConfig.id === id ? sortConfig.direction : undefined;
    };

    const handleSort = (idx, e) => {
        switch (idx) {
            case 0:
                requestSort('subject', idx);
                break;
            case 1:
                requestSort('applier', idx);
                break;
            case 2:
                requestSort('apply_date', idx);
                break;
            case 3:
                requestSort('submit_date', idx);
                break;
            case 4:
                requestSort('presented_file', idx);
                break;
            case 5:
                requestSort('additional_action', idx);
                break;
            case 6:
                requestSort('status', idx);
                break;
            case 7:
                requestSort('protocol_number', idx);
                break;
            case 8:
                requestSort('protocol_date', idx);
                break;
            case 9:
                requestSort('applier', idx);
                break;
            case 10:
                requestSort('applier', idx);
                break;
            case 11:
                requestSort('applier', idx);
                break;
            case 12:
                requestSort('applier', idx);
                break;
            case 13:
                requestSort('applier', idx);
                break;
            case 14:
                requestSort('applier', idx);
                break;
            case 15:
                requestSort('applier');
        }
        console.log(idx);
    };

    const handleShowAttachments = async (stateId, mid) => {
        const result = await getAttachments(mid);
        const attachments = result.map(x => {
            const name = x.AttchName;
            const index = name.lastIndexOf('_');
            const cutName = name.substring(0, index);
            x.AttchName = cutName;
            return x;
        });
        setAttachments(attachments);
        showAttachments();
    };

    const showAttachments = () => {
        window.JQUERY("#attachmentModal").modal('show');
    }
    const hideAttachments = () => {
        window.JQUERY("#attachmentModal").modal('hide');
    }

    const handleSortStatus = name => {
        let updatedData = [];

        data.map(node => {
            if (node.status.toLowerCase() === name) {
                updatedData.push(node);
            }
        });
        console.log('hey yo yo yo');
        setFilteredData(updatedData);
    };

    const handleExportToExcel = () => {
        let data = [
            {
              sheet: "Muraciətlər",
              columns: [
                { label: "Mövzu", value: "subject" }, 
                { label: "Müraciət edən", value: "whoApplied" },
                { label: "Müraciət tarixi", value: "applyDate" },  
                { label: "Müraciətin Korporativ Katibə təqdim edilməsi", value: "toSecretary" },
                { label: "Əlavə kommunikasiya tədbirləri", value: "additionalCommn" },
                { label: "MŞ-nın qərarının statusu", value: "status" },
                { label: "Protokol №", value: "protocol" },
                { label: "Təsdiq tarixi", value: "approveDate" },
                { label: "Protokol daxil olub", value: "protocolDate" },
                { label: "Qeyd", value: "note1" },
                { label: "MŞ-nın qərarından irəli gələn tapşırıqlar", value: "tasksByBoard" },
                { label: "Tapşırıqların icrası istiqamətində KİM tərəfindən atılan addımlar", value: "stepsByKim" },
                { label: "MŞ-nın qərarından irəli gələn tapşırıqların icra statusu", value: "tasksStatus" },
                { label: "Qeyd 2", value: "note2" },
                { label: "Qeyd 3", value: "noteByIah" }
              ]
            }
          ]
          const content = [];
          for (const element of stateDataAll) {
                const row = {
                      subject: element.SUBJECT !== '?' ? element.SUBJECT: '',
                      whoApplied: element.WHOAPPLIED !== '?' ? element.WHOAPPLIED: '',
                      applyDate: element.APPLYDATE !== '?' ? element.APPLYDATE : '',
                      toSecretary: element.TOSECRETARY !== '?' ? element.TOSECRETARY : '',
                      additionalCommn: element.ADDITIONALCOMMN !== '?' ? element.ADDITIONALCOMMN : '',
                      status: element.STATUS !== '?' ? element.STATUS : '',
                      protocol: element.PROTOCOL !== '?' ? element.PROTOCOL: '',
                      approveDate: element.APPROVEDATE !== '?' ? element.APPROVEDATE : '',
                      note1: element.NOTE1 !== '?' ? element.NOTE1 : '',
                      tasksByBoard: element.TASKSBYBOARD !== '?' ? element.TASKSBYBOARD : '',
                      stepsByKim: element.STEPSBYKIM !== '?' ? element.STEPSBYKIM : '',
                      tasksStatus: element.TASKSTATUS !== '?' ? element.TASKSTATUS : '',
                      note2: element.NOTE2 !== '?' ? element.NOTE2 : '',
                      noteByIah: element.NOTEBYAIH !== '?' ? element.NOTEBYAIH : '',
                  };
                content.push(row);
          }
          if(content.length > 0){
            data[0].content = content;
          
            let settings = {
              fileName: "Appeal", // Name of the resulting spreadsheet
              extraLength: 3, // A bigger number means that columns will be wider
              writeOptions: {}, // Style options from https://github.com/SheetJS/sheetjs#writing-options
            }

            xlsx(data, settings);
          }   
    }

    const args = {
        states,
        filteredData,
        items,
        setFilteredData,
        tableColumns,
        addNew,
        clickAndDrag,
        handleSort,
        getClassNamesFor,
        showAttachments: handleShowAttachments,
        hideAttachments,
        attachments,
        handleSortStatus,
        exportToExcel: handleExportToExcel
    };

    return <TableComponent {...args} />;
};

export default Table;
