import InputLine from '../../../components/InputLine';
import HeaderLine from '../../../components/HeaderLine';
import Accordion from '../components/Accordion';
import Attachments from '../components/Attachments';

const TableComponent = (props) => {
    return <>
        <h1 className="app--header">Socar Online Platform</h1>
        {
            props.filteredData ? (
                <>
                    <div
                        className="main-container"
                        onClick={e => props.clickAndDrag(e)}
                    >
                        <HeaderLine
                            columnHeaders={props.tableColumns}
                            onClick={props.handleSort}
                            getClassNamesFor={props.getClassNamesFor}
                        />
                        <InputLine
                            columnHeaders={props.tableColumns}
                            onInput={props.checkData}
                        />
                        {props.states.map(x => {
                            let args = {
                                id: x.id,
                                name: x.name,
                                filteredData: props.items,
                                tableColumns: props.tableColumns,
                                addNew: props.addNew,
                                handleSort: props.handleSort,
                                showAttachments: props.showAttachments,
                                handleSortStatus: props.handleSortStatus,
                            };
                            return <Accordion key={x.id} {...args} />;
                        })}
                    </div>
                    <button className="btn btn-primary positioned-button">
                        Add New
                    </button>
                    <button className="btn btn-success positioned-button"
                            onClick={props.exportToExcel}>
                        Export
                    </button>
                </>
            ) : (
                <div className="no-data">
                    <p className="no-data--paragraph">
                        Bazada heç bir məlumat yoxdur.
                    </p>
                    <button className="btn btn-primary">Add New</button>
                </div>
            )}
            <Attachments hide={props.hideAttachments} attachments={props.attachments} />
        </>
};

export default TableComponent;
