const handleGetStates = async (set, get) => {
    try{
        const ticket = get().ticket;
        const myHeaders = new Headers();
        myHeaders.append("OTCSTicket", ticket);

        const requestOptions = {
          method: 'GET',
          headers: myHeaders,
          redirect: 'follow'
        };

        const response = await fetch("https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/186063405/output?format=json", requestOptions)
        const json = await response.json();
        const data = JSON.parse(json.data).result;
        data.pop();
        set({
            states: data
        })
    }catch(e){
        console.error(e);
    }   
}

const handleGetStateData = async (set, get) => {
    try{
        const ticket = get().ticket;
        const myHeaders = new Headers();
        myHeaders.append("OTCSTicket", ticket);

        const requestOptions = {
          method: 'GET',
          headers: myHeaders,
          redirect: 'follow'
        };

        const response = await fetch("https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/186205572/output?format=json", requestOptions)
        const json = await response.json();
        const data = JSON.parse(json.data).result;
        data.pop();
        let groupByData = _.groupBy(data, 'NZID');
        for (const key in groupByData) {
            if (Object.hasOwnProperty.call(groupByData, key)) {
                const COUNTS = {
                    countAwait: 0,
                    countApproved: 0
                };
                groupByData[key] = groupByData[key].map(node => {
                    if (node.STATUS == 'Gözləmədədir') {
                        COUNTS.countAwait++;
                    }
                    if (node.STATUS == 'Təsdiq edilib') {
                        COUNTS.countApproved++;
                    }
                    return node;
                });
                groupByData[key].push(COUNTS);
            }
        }
        
        set({
            stateDataAll: data,
            stateData: groupByData
        })
    }catch(e){
        console.error(e);
    }
}

const handleCreateFiles = async (set, get) => {
    const createFile = async (file) => {
		const myHeaders = new Headers();
		myHeaders.append("OTCSTicket", window.OTCSTicket);

		const formdata = new FormData();
		formdata.append("type", "144");
		formdata.append("parent_id", "186174338");
		formdata.append("name", file.name + '_' + uuid());
		formdata.append("file", file);

		const requestOptions = {
  			method: 'POST',
  			headers: myHeaders,
  			body: formdata,
  			redirect: 'follow'
		};
        try{
            const response = await fetch("https://ecm.socar.az/otcs/llisapi.dll/api/v2/nodes", requestOptions);
  		    const json = await response.json();
            return json.results.data.properties.id;
        }catch(e){
            alert(file.name + "faylını əlavə edəndə səhv baş verdi !");
            throw e;
        }
	}
    const createdFiles = get().files;
    let createdFileIds = get().createdFileIds;
	for await (const el of createdFiles) {
        try {
            const id = await createFile(el);
            createdFileIds.push(id.toString());
        } catch (error) {
            console.error(error);
        }	
	}
    set({
        createdFileIds
    })
}

