const AccordionItem = props => {
    return (
        <div
            className={`item item-main accordion ${
                props.accordion ? 'item-main--open' : ''
            }`}
        >
            <span className="accordion">{props.element}</span>
            <div className="status-wrapper">
                <button
                    data-tooltip="Gözləmədədir"
                    id="gözləmədədir"
                    onClick={e => props.handleSortStatus(e.target.id)}
                    className="status-button status-await"
                >
                    {props.countAwait}
                </button>
                <button
                    data-tooltip="Təsdiqlənmişdir"
                    id="təsdiq edilib"
                    onClick={e => props.handleSortStatus(e.target.id)}
                    className="status-button status-approved"
                >
                    {props.countApproved}
                </button>
            </div>
            <div className="button-wrapper">
                <button className="btn-new span--arrow" onClick={props.onClick}>
                    <i
                        className={`fas fa-angle-${
                            props.accordion ? 'up' : 'down'
                        }`}
                    ></i>
                </button>
                <button className="btn-new" onClick={props.addNewItem}>
                    <i className="fas fa-plus"></i>
                </button>
            </div>
        </div>
    );
};

export default AccordionItem;
